import React, { useState } from 'react'

const Note = ({ deleteMethod, toggleMethod, text, completed }) => {
  const [editable, setEditable] = useState(false)
  const [value, setValue] = useState(text)
  const classString = completed ? 'note completed' : 'note'
  const handleEdit = event => {
    setEditable(!editable)
    debugger
    inputRef.current.focus()
  }
  let inputRef
  return (
    <div className={classString}>
      <input ref={input => (inputRef = input)} type="text" value={value} disabled={!editable} />
      <button onClick={handleEdit}>{editable ? 'Save' : 'Edit'}</button>
      <button onClick={() => toggleMethod()}>Done</button>
      <button onClick={() => deleteMethod()}>Delete</button>
    </div>
  )
}

export default Note
